@mixin icon-structure($iconPath, $width, $height, $filter) {
    display: inline-block;
    width: $width;
    height: $height;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url($iconPath);
    filter: $filter;

    &:hover{
        cursor: pointer;
    }
}

.logo{
    @include icon-structure("./static/logo.svg", 30px, 30px, $white-filter);
}

